import React from 'react'
import Image from 'gatsby-image'

import { IBioImagesMarkdownRemark, IBioImagesEdge } from '../../types/types'
import { ImageContainer } from './ImageContainer'
import { cleanAndCapitalize } from '../../utils'

import { Blend } from '../Blend'
import { Filter } from '../Filter'
import { ImageGrid } from '../ImageGrid'

export function GamesGrid(props: {
  wallpapers: IBioImagesMarkdownRemark
}): JSX.Element {
  return (
    <ImageGrid rows={2} columns={7}>
      {props.wallpapers.edges.map(
        (edge: IBioImagesEdge, i: number): JSX.Element => (
          <ImageContainer
            overlay={{
              node: (
                <Blend mode={'difference'}>
                  <h3 style={{ color: 'white' }}>
                    {cleanAndCapitalize(edge.node.name)}
                  </h3>
                </Blend>
              ),
            }}
            key={i}
          >
            <Filter>
              <Image fluid={edge.node.childImageSharp.fluid} />
            </Filter>
          </ImageContainer>
        )
      )}
    </ImageGrid>
  )
}
